import {Const} from "../../../utils/Const";

export default {
	data() {
		return {
			avatar: ""
		}
	},

	props: {
		headImg: {
			type: String,
			default: Const.imDefaultHeadImg
		},
	},

	mounted() {
		this.avatar = this.headImg;
	},

	watch: {
		headImg() {
			this.avatar = this.headImg;
		}
	},

	methods: {
		imgError() {
			this.avatar = Const.imDefaultHeadImg;
		}
	}
}
